import styled from "styled-components"

export const Wrapper = styled.div`

    a {
        color: ${props => props.theme.link.normal };
        display: inline-block;
        font-family: var(--family-heading);
        font-size: 16px;
        font-weight: 600;
        line-height: var(--line-height-tight);
        margin: -8px;
        padding: 8px;
        position: relative;
        text-decoration: none;

        @media (min-width: 800px) {
            font-size: 18px;
        }

        @media (min-width: 2000px) {
            font-size: 20px;
        }

    
        &:visited {
            color: ${props => props.theme.link.normal };
        }
    
        &:hover {
            color: ${props => props.theme.link.hover };
    
            path {
                stroke: ${props => props.theme.link.hover };
            }
    
        }
    
        svg {
            display: inline;
            fill: none;
            margin-left: 8px;
            position: relative;
            top: 5px;
            width: 12px;

            @media (min-width: 800px) {
                top: 4px;
                width: 16px;
            }
    
            path {
                stroke: ${props => props.theme.link.normal };
                stroke-width: 3;    
            }
    
        }

    }

`