import React from "react"
import * as Styled from "./styled"
import { Fade } from "../Animations"
import { useInView } from "react-intersection-observer"
import Button from "../Button"
import Container from "../Container"
import Grid from "../Grid"
import Image from "../Image"

const WorkCard = (props) => {

    // Intersection Observer

    const [ref, inView] = useInView({
        rootMargin: "-160px 0px",
        triggerOnce: true,
    })

    // Animations

    const ImageAnimation = {
        initial: {
            width: 0,
        },
        visible: {
            width: inView ? "auto" : 0,
            transition: {
                duration: 0.75,
                ease: [0.85, 0, 0.35, 1],
                type: "tween"
            },
        },
        exit: {
            width: 0,
            transition: {
                duration: 0.75,
                ease: [0.85, 0, 0.35, 1],
                type: "tween"
            },
        },
    }


    return (
        <Styled.Wrapper>
            <Container hasVerticalLine={false}>
                <Grid alignItems="center">
                    <Styled.ContentCell>
                        <Fade>
                            <Styled.Meta>{props.dateStart === props.dateEnd ? props.dateEnd : `${props.dateStart} - ${props.dateEnd}`}</Styled.Meta>
                            <Styled.Title>{props.title}</Styled.Title>
                            <Styled.Description>{props.description}</Styled.Description>
                            <Button to={props.slug} color={props.buttonColor}>{props.cta}</Button>
                        </Fade>
                    </Styled.ContentCell>
                    <Styled.ImageCell>
                        <Styled.ImageWrapper
                            ref={ref}
                            initial="initial"
                            animate="visible"
                            exit="exit"
                            variants={ImageAnimation}
                        >
                            <Image src={props.image} alt={props.imageAlt} objectPosition={props.imagePosition} />
                        </Styled.ImageWrapper>
                    </Styled.ImageCell>
                </Grid>
            </Container>
        </Styled.Wrapper>
    )
}

export default WorkCard