import React from "react"
import * as Styled from "./styled"
import { Link } from "gatsby"
import { motion } from "framer-motion"

const linkParentAnimation = {
    initial: {},
    completed: {}
}

const linkChildAnimation = {
    initial: {
        x: 0
    },
    completed: {
        x: 8,
    }
}


export default (props) => {
    return (
        <Styled.Wrapper>
            <Link to={props.to}>
                <motion.div animate="initial" whileHover="completed" initial="initial" variants={linkParentAnimation} >
                    {props.children}
                    <motion.svg variants={linkChildAnimation} width="20" height="21" viewBox="0 0 20 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M8.48535 2L16.9706 10.4853L8.48535 18.9706"/>
                        <path d="M1 10.5H17"/>
                    </motion.svg>
                </motion.div>
            </Link>
        </Styled.Wrapper>
    )
}